"use client";

import React, { useCallback, useEffect, useMemo, useState } from "react";
import {
  type Report,
  type ReportStats,
  STORAGE_KEY,
  TASKS_KEY_PREFIX,
  getWeekRange,
  formatWeekRangeTitle,
  sortReportsDesc,
  loadTasks,
  buildStats,
  findThisWeekReport,
} from "@/lib/reportUtils";

import { ReportsHeader } from "@/components/ReportsHeader";
import { DashboardStats } from "@/components/DashboardStats";
import { ReportList } from "@/components/ReportList";

export default function ReportsPage() {
  const [reports, setReports] = useState<Report[]>([]);
  const [loaded, setLoaded] = useState(false);

  // ÈÄ±„Åî„Å®„ÅÆÈõÜË®à„Ç≠„É£„ÉÉ„Ç∑„É•
  const [statsById, setStatsById] = useState<Record<string, ReportStats>>({});

  // ÂàùÂõûË™≠„ÅøËæº„ÅøÔºölocalStorage „Åã„Çâ‰∏ÄË¶ß„ÇíË™≠„ÇÄ
  useEffect(() => {
    if (typeof window === "undefined") return;

    const raw = window.localStorage.getItem(STORAGE_KEY);

    if (!raw) {
      // ÂàùÊúü„Çµ„É≥„Éó„É´ÔºàÂÖ®ÈÉ®„Äå‰ªäÈÄ±„ÄçÔºâ
      const now = new Date();
      const { start, end } = getWeekRange(now);
      const weekStartIso = start.toISOString();
      const weekEndIso = end.toISOString();
      const title = formatWeekRangeTitle(weekStartIso, weekEndIso);
      const createdAt = now.toISOString();

      const initial: Report[] = [
        {
          id: "1",
          title,
          description: "„Åì„ÅÆÈÄ±„Å´Âèñ„ÇäÁµÑ„Çì„Å†„Çø„Çπ„ÇØ„ÇÑÂ≠¶„Å≥„ÇíÊï¥ÁêÜ„Åó„Åæ„Åô„ÄÇÔºà„Çµ„É≥„Éó„É´Ôºâ",
          createdAt,
          weekStart: weekStartIso,
          weekEnd: weekEndIso,
        },
        {
          id: "2",
          title,
          description: "Âºï„ÅçÁ∂ö„Åç„ÄÅ‰ªäÈÄ±„ÅÆ„Ç¢„Ç¶„Éà„Éó„ÉÉ„Éà„ÇíË®òÈå≤„Åó„Åæ„Åô„ÄÇÔºà„Çµ„É≥„Éó„É´Ôºâ",
          createdAt,
          weekStart: weekStartIso,
          weekEnd: weekEndIso,
        },
        {
          id: "3",
          title,
          description: "„Çà„ÇäÂÖ∑‰ΩìÁöÑ„Å™ÊàêÊûú„ÇÑÂèçÁúÅÁÇπ„ÇíÊõ∏„ÅçÂá∫„Åó„Å¶„Åø„Åæ„Åó„Çá„ÅÜ„ÄÇÔºà„Çµ„É≥„Éó„É´Ôºâ",
          createdAt,
          weekStart: weekStartIso,
          weekEnd: weekEndIso,
        },
      ];

      setReports(sortReportsDesc(initial));
      setLoaded(true);
      return;
    }

    try {
      const parsed = JSON.parse(raw) as any[];
      if (Array.isArray(parsed)) {
        const normalized: Report[] = parsed.map((r, index) => {
          const id = String(r.id ?? index + 1);

          const createdAt =
            typeof r.createdAt === "string" && r.createdAt ? r.createdAt : new Date().toISOString();

          const createdDate = new Date(createdAt);
          const baseDate = Number.isNaN(createdDate.getTime()) ? new Date() : createdDate;

          let weekStartIso: string;
          let weekEndIso: string;

          if (typeof r.weekStart === "string" && typeof r.weekEnd === "string") {
            weekStartIso = r.weekStart;
            weekEndIso = r.weekEnd;
          } else {
            const { start, end } = getWeekRange(baseDate);
            weekStartIso = start.toISOString();
            weekEndIso = end.toISOString();
          }

          const title = formatWeekRangeTitle(weekStartIso, weekEndIso);

          const description =
            typeof r.description === "string" && r.description.trim() !== ""
              ? r.description
              : "„Åì„ÅÆÈÄ±„ÅÆ„Çø„Çπ„ÇØ„ÇÑÊåØ„ÇäËøî„Çä„ÇíË®òÈå≤„Åó„Åæ„Åô„ÄÇ";

          return {
            id,
            title,
            description,
            createdAt,
            weekStart: weekStartIso,
            weekEnd: weekEndIso,
          };
        });

        setReports(sortReportsDesc(normalized));
      } else {
        setReports([]);
      }
    } catch (e) {
      console.error("failed to parse reports from localStorage", e);
      setReports([]);
    }

    setLoaded(true);
  }, []);

  // ‰∏ÄË¶ß„ÅåÂ§â„Çè„Çã„Åü„Å≥„Å´‰øùÂ≠ò
  useEffect(() => {
    if (!loaded) return;
    if (typeof window === "undefined") return;
    window.localStorage.setItem(STORAGE_KEY, JSON.stringify(reports));
  }, [reports, loaded]);

  // ÈõÜË®à„Çí‰Ωú„ÇãÔºàÊàª„Å£„Å¶„Åç„Åü„Å®„Åç„ÇÇÊúÄÊñ∞Âåñ„Åô„Çã„Åü„ÇÅ focus „ÅßÂÜçË®àÁÆóÔºâ
  const refreshStats = useCallback(() => {
    if (typeof window === "undefined") return;
    const nowMs = Date.now();

    const next: Record<string, ReportStats> = {};
    for (const r of reports) {
      const tasks = loadTasks(r.id, nowMs);
      next[r.id] = buildStats(tasks);
    }
    setStatsById(next);
  }, [reports]);

  useEffect(() => {
    if (!loaded) return;

    refreshStats();

    const onFocus = () => refreshStats();
    window.addEventListener("focus", onFocus);
    return () => window.removeEventListener("focus", onFocus);
  }, [loaded, refreshStats]);

  // Êñ∞„Åó„ÅÑÈÄ±„ÇíËøΩÂä†
  const handleAddReport = () => {
    setReports((prev) => {
      const numericIds = prev.map((r) => Number(r.id)).filter((n) => !Number.isNaN(n));
      const nextIdNum = numericIds.length > 0 ? Math.max(...numericIds) + 1 : 1;
      const nextId = String(nextIdNum);

      const now = new Date();
      const { start, end } = getWeekRange(now);
      const weekStartIso = start.toISOString();
      const weekEndIso = end.toISOString();

      const newReport: Report = {
        id: nextId,
        title: formatWeekRangeTitle(weekStartIso, weekEndIso),
        description: "Êñ∞„Åó„Åè‰ΩúÊàê„Åó„ÅüÈÄ±„ÅÆ„Çø„Çπ„ÇØ„ÇÑÊåØ„ÇäËøî„Çä„ÇíË®òÈå≤„Åß„Åç„Åæ„Åô„ÄÇ",
        createdAt: now.toISOString(),
        weekStart: weekStartIso,
        weekEnd: weekEndIso,
      };

      const next = [newReport, ...prev];
      return sortReportsDesc(next);
    });
  };

  // ÈÄ±Â†±ÂâäÈô§ÔºàÁ¢∫Ë™ç„ÉÄ„Ç§„Ç¢„É≠„Ç∞‰ªò„ÅçÔºâ
  const handleDelete = (reportId: string, e: React.MouseEvent) => {
    if (typeof window !== "undefined") {
      const ok = window.confirm(
        "Êú¨ÂΩì„Å´„Åì„ÅÆ„É¨„Éù„Éº„Éà„ÇíÂâäÈô§„Åó„Å¶„Çà„Çç„Åó„ÅÑ„Åß„Åô„ÅãÔºü\nÔºà„Åì„ÅÆ„É¨„Éù„Éº„Éà„Å´Á¥ê„Å•„Åè„Çø„Çπ„ÇØ„Å®„É°„É¢„ÇÇÂâäÈô§„Åï„Çå„Åæ„ÅôÔºâ"
      );
      if (!ok) return;

      // „Åù„ÅÆÈÄ±Â†±„ÅÆ„Çø„Çπ„ÇØÔºÜ„É°„É¢„ÇÇÂâäÈô§
      window.localStorage.removeItem(`${TASKS_KEY_PREFIX}${reportId}`);
      window.localStorage.removeItem(`weekly-report-note-${reportId}`);
    }

    setReports((prev) => prev.filter((r) => r.id !== reportId));
  };

  const thisWeekReport = useMemo(() => findThisWeekReport(reports), [reports]);
  const thisWeekStats = thisWeekReport ? statsById[thisWeekReport.id] : null;

  return (
    <main className="min-h-screen bg-slate-50/50">
      <div className="mx-auto max-w-5xl py-8 px-4 md:px-8 space-y-10">
        <ReportsHeader
          reportCount={reports.length}
          loading={!loaded}
          onAddReport={handleAddReport}
        />

        <DashboardStats
          thisWeekReport={thisWeekReport}
          thisWeekStats={thisWeekStats}
          onRefresh={refreshStats}
        />

        <ReportList
          reports={reports}
          statsById={statsById}
          onDelete={handleDelete}
        />
      </div>
    </main>
  );
}
